<template>
  <div>
    <LayoutBox one="邮费管理" two="平台收商家邮费">
      <el-form ref="form" :model="postFee" label-width="140px" size="small">
        <el-form-item label="小超运费收取规则：">
          <span>基础重量：{{ postFee.basicsWeight }}kg，基础费用：{{ postFee.basics }}元，超过部分按每千克{{ postFee.overWeight }}元计算</span>
        </el-form-item>
        <el-form-item>
          <el-button @click="editSuperFormDialogVisible = true">编辑小超运费规则</el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>

    <edit-super-form-cmp
      :dialogVisible="editSuperFormDialogVisible"
      :form="form"
      @close="editSuperFormDialogVisible = false"
    />
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import EditSuperFormCmp from './edit_super_form_cmp/index.vue'
import { MIXINS_ADMINPOSTFINDBYID_API } from '@/mixins/api'

export default {
  name: 'postFeeAdmin',

  components: {
    LayoutBox,
    EditSuperFormCmp
  },

  mixins: [
    MIXINS_ADMINPOSTFINDBYID_API
  ],

  data () {
    return {
      form: {},
      editSuperFormDialogVisible: false,
      postFee: {}, // 邮费
    }
  },

  created () {
    this.adminPostFindById({}).then((res) => {
      this.postFee = res.data.data
      this.form = JSON.parse(JSON.stringify(this.postFee))
    })
  },
}
</script>

