<template>
  <div>
    <el-dialog
      title="编辑小超运费规则"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" label-width="120px" size="small">
        <el-form-item label="基础重量：">
          <el-input v-model="form.basicsWeight" placeholder="请输入基本重量"><template slot="append">kg</template></el-input>
        </el-form-item>
        <el-form-item label="基础费用：">
          <el-input v-model="form.basics" placeholder="请输入基础费用："><template slot="append">元</template></el-input>
        </el-form-item>
        <el-form-item label="超过部分费用：">
          <el-input v-model="form.overWeight" placeholder="请输入基础费用："><template slot="append">元/kg</template></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="onSave">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { MIXINS_ADMINPOSTUPDATE_API } from '@/mixins/api'

export default {
  name: 'editSuperForm',

  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    },
    form: {
      type: Object,
      default: () => {
        return {
          basicsWeight: '',
          basics: '',
          overWeight: '',
        }
      }
    }
  },

  mixins: [
    MIXINS_ADMINPOSTUPDATE_API
  ],

  data() {
    return {
    }
  },
  methods: {
    handleClose () {
      this.$emit('close')
    },

    // 点击保存
    onSave () {
      const data = {
        ...this.form
      }
      this.adminPostUpdate(data).then((res) => {
        if (res.data.success) {
          this.handleClose()
          this.reload()
          this.$message.success('保存成功')
        } else {
          this.$message.error(res.data.msg)
        }
      })
    }
  },

  inject: ['reload']
}
</script>

<style lang="scss" scoped>

</style>
